body.is-teacher .out { width: 100%; display: inline-table; }

.rightbar { padding-top: 40px; height: 100%; width: 89%; vertical-align: top; display: table-cell; }
@media only screen and (max-width: 1280px) { .rightbar { width: calc(100% - 140px); } }
.rightbar .l, .rightbar .header { margin: 0 auto; padding: 0 88px 0 103px; max-width: 1150px; }
.rightbar .l { padding-bottom: 40px; }
.rightbar .l__center { margin-top: 50px; }
.rightbar .l__center .title-square { margin-top: 55px; }
.rightbar .l__center .title-square + .date { margin-top: -12px; line-height: 1; }
.rightbar .l__center .title-square:first-child { margin-top: 0; }
.rightbar .l__center .date { text-align: right; display: block; }
.rightbar .l__center .accord { margin-top: 20px; }
.rightbar .l__center .accord:first-child { margin-top: 0; }
.rightbar .stat { margin-top: 40px; }

.sidebar { padding: 68px 20px 10px; width: 11%; height: 100%; color: #fff; text-align: center; vertical-align: top; display: table-cell; background: #2a2a2a; }
@media only screen and (max-width: 1280px) { .sidebar { width: 140px; } }
.sidebar .nav { margin-top: 126px; }

.nav { font-size: 12px; }
.nav li { margin-top: 28px; display: block; }
.nav li:first-child { margin-top: 0; }
.nav li.is-active { font-family: "Lato-Black", Arial, sans-serif; }
.nav li.is-active span { border-width: 2px; border-color: #fff; }
.nav a { text-decoration: none; }
.nav a:hover span { border-color: #fff; }
.nav a span { transition: border-color 0.2s; border-bottom: 1px solid transparent; }

.hours { overflow: hidden; font-size: 0; }
.hours__in { margin: -2% -2% 0 0; }

.hour { margin: 2% 2% 0 0; width: 31.3%; color: #706f6f; font-size: 12px; vertical-align: top; transition: background 0.2s; background: #ddd; display: inline-block; }
.hour:hover, .hour.is-active { background: #ffe94a; }
.hour a { padding: 6px 14px 6px 11px; display: block; *zoom: 1; }
.hour a:after { content: " "; display: table; clear: both; }
.hour .time { float: right; letter-spacing: 1px; }
.hour .group-name { font-family: "Lato-Black", Arial, sans-serif; }

.visiting { color: #1d1d1b; opacity: 0.2; cursor: pointer; user-select: none; font-family: "Lato-Bold", Arial, sans-serif; font-size: 14px; will-chenge: opacity; transform: translate3d(0, 0, 0); transition: opacity 0.2s; }
.visiting:hover, .visiting.is-checked { opacity: 1; }

.stat { margin-right: 40px; text-align: right; font-size: 12px; }
