div.ui-datepicker { width: 18em; border: none; }
div.ui-datepicker .ui-datepicker-header { padding: 0; border: none; background: transparent; *zoom: 1; }
div.ui-datepicker .ui-datepicker-header:after { content: " "; display: table; clear: both; }
div.ui-datepicker .ui-datepicker-title { float: left; padding-right: 62px; margin: 0; font-size: 18px; font-family: "Lato-Black", Arial, sans-serif; }
div.ui-datepicker .ui-datepicker-prev, div.ui-datepicker .ui-datepicker-next { top: 3px; cursor: pointer; }
div.ui-datepicker .ui-datepicker-prev:hover, div.ui-datepicker .ui-datepicker-next:hover { border: none; background: transparent; }
div.ui-datepicker .ui-datepicker-prev { left: auto; right: 27px; }
div.ui-datepicker .ui-datepicker-prev .ui-icon { background: url("../img/svg/calendar-left.svg") no-repeat 0 0; background-size: 19px 19px; width: 19px; height: 19px; display: inline-block; }
div.ui-datepicker .ui-datepicker-next { right: 2px; }
div.ui-datepicker .ui-datepicker-next .ui-icon { background: url("../img/svg/calendar-right.svg") no-repeat 0 0; background-size: 19px 19px; width: 19px; height: 19px; display: inline-block; }
div.ui-datepicker .ui-datepicker-year { display: none; }
div.ui-datepicker .ui-state-default, div.ui-datepicker .ui-widget-content .ui-state-default, div.ui-datepicker .ui-widget-header .ui-state-default { height: 2.7em; box-sizing: border-box; border-color: #ffe94a; background: #ffe94a; }
div.ui-datepicker .ui-state-highlight { color: #000; border-color: #fff; background: #fff; }
div.ui-datepicker table { color: #000; margin-top: 9px; font-size: 11px; font-family: "Lato-Black", Arial, sans-serif; }
div.ui-datepicker table span, div.ui-datepicker table a { padding: 1em 0.2em 0.2em 0.2em; text-align: left; }
div.ui-datepicker thead { text-transform: uppercase; font-size: 9px; font-family: "Lato-Black", Arial, sans-serif; border-bottom: 3px solid #fff; background: #ffe94a; }
div.ui-datepicker thead th { padding: 0.56em 0.3em; }


