// out
.out
	padding-bottom: 54px
	min-height: 100%
	height: 100%
	height: auto

// l
.l
	+clr
	&__in
		+clr
	&__center
		margin: 0 auto
		max-width: 486px
	.title-little
		margin: 32px 0 12px
		&:first-child
			margin-top: 0
.l-col1
	float: left
	width: 52%
	& > .title-large
		line-height: 0.7
	.title-large
		color: $yellow
.l-col2
	float: right
	padding-left: 32px
	width: 48%

// logo
.logo
	transform: translate3d(0,0,0)
	+svg(logo,158,172)
	&.is-teacher
		+svg(logo-teacher,51,56)
	+r(768)
		+svg(logo,117,128)

// input
.input
	padding: 0 20px
	height: 30px
	width: 100%
	+lr
	color: rgba($black, .4)
	letter-spacing: 0.05em
	font-size: 16px
	border-radius: 4px
	border: 2px solid $black
	background: transparent
	display: block
	+r(768)
		padding: 0 10px
		font-size: 12px
	&:focus
		&::placeholder
			font-size: 0
	&::placeholder
		color: rgba($black, .4)

// btn
.btn
	padding: 8px 30px
	color: $white
	+lb
	font-size: 11px
	text-transform: uppercase
	transition: background .2s, color .2s
	border-radius: 4px
	letter-spacing: .1em
	line-height: 1.3
	border: 2px solid $black
	background: $black
	display: inline-block
	&:hover
		color: $black
		background: transparent
	&_yellow
		color: $black
		border-color: $yellow
		background: $yellow
		&:hover
			color: $yellow
	&_red
		border-color: $red
		background: $red
		&:hover
			color: $red
	&_small
		padding: 6px 17px

// field
.field
	margin-top: 23px
	+clr
	&:first-child
		margin-top: 0
	.input
		float: right
		padding: 0 10px
		height: 43px
		width: calc(100% - 43px)
		border-color: $yellow
		border-radius: 0 4px 4px 0
	&__label
		float: left
		height: 43px
		width: 43px
		line-height: 41px
		font-size: 26px
		text-align: center
		cursor: pointer
		user-select: none
		border-radius: 4px 0 0 4px
		background: $yellow
		.icon
			vertical-align: middle
			display: inline-block

// soc
.soc
	font-size: 0
	.icon
		font-size: 25px
		vertical-align: middle
		+r(768)
			font-size: 18px
		&-vk
			font-size: 14px
			+r(768)
				font-size: 12px
	li
		margin-left: 36px
		vertical-align: top
		display: inline-block
		+r(768)
			margin-left: 25px
		&:first-child
			margin-left: 0
	a
		height: 41px
		width: 41px
		text-align: center
		color: $yellow
		line-height: 39px
		transition: background .2s, color .2s
		border-radius: 50%
		border: 2px solid $black
		background: $black
		display: block
		+r(768)
			width: 32px
			height: 32px
			line-height: 28px
		&:hover
			color: $black
			background: transparent

// checkbox
.checkbox
	position: relative
	cursor: pointer
	user-select: none
	input,
	span:before
		position: absolute
		top: 1px
		left: 0
		width: 10px
		height: 10px
	input
		z-index: 2
		opacity: 0
		cursor: pointer
		&:checked
			& + span
				&:after
					display: block
	span
		position: relative
		padding-left: 20px
		font-size: 11px
		&:before
			content: ''
			border: 1px solid $black
		&:after
			position: absolute
			content: ''
			top: 0
			left: 2px
			+svg(checkbox-arr, 10,7)
			display: none


// label
.label
	margin-bottom: 5px
	+lb
	cursor: pointer
	user-select: none
	display: block

// forgot
.forgot
	&:hover
		span
			border-color: transparent
	span
		transition: border-color .2s
		border-bottom: 1px solid $black

// title
.title
	+rb
	font-size: 48px
	line-height: 1.1
	letter-spacing: .14em
	+r(768)
		font-size: 28px

.title-large
	+rb
	font-size: 57px
	line-height: 1
	letter-spacing: 0.015em
	&.is-yellow
		color: $yellow

.title-big
	font-size: 72px
	line-height: 1.25
	letter-spacing: .1em
	text-transform: uppercase
	+lb
h1,
.title-middle
	+lb
	font-size: 36px
	letter-spacing: .11em
	+r(768)
		font-size: 24px

.title-medium
	font-size: 29px
	line-height: 1.3
	+lb

.title-small
	font-size: 24px
	+lb
	letter-spacing: .05em
	+r(768)
		font-size: 18px

.title-little
	font-size: 18px
	+lb
	line-height: 1.2

.title-petty
	font-size: 16px
	+lb

.title-square
	position: relative
	padding: 10px 10px 10px 25px
	+lb
	font-size: 14px
	line-height: 1
	text-transform: uppercase
	&:before
		position: absolute
		content: ''
		top: 0
		left: 0
		width: 35px
		height: 35px
		background: $yellow
	span
		position: relative

// mouse
.mouse
	font-size: 0
	vertical-align: top
	display: inline-block
	&:hover
		.icon
			animation: mouse 1s infinite linear
	&__text
		margin-top: 5px
		letter-spacing: .1em
		font-size: 11px
		+lb
		display: block
	.icon
		font-size: 47px
		transform: translate3d(0,0,0)

@keyframes mouse
	0%
		transform: translateY(0%)
	25%
		transform: translateY(6%)
	50%
		transform: translateY(0%)
	75%
		transform: translateY(6%)
	100%
		transform: translateY(0)

// picture
.picture
	position: relative
	padding: 74px 0
	height: 382px
	color: $white
	+lb
	letter-spacing: 0.2em
	line-height: 1.2
	text-transform: uppercase
	background-size: cover
	background-repeat: no-repeat
	background-position: 50% 0
	background-attachment: fixed
	z-index: 2
	background-color: $bg-pic
	+r(480)
		min-height: 382px
		height: auto
		background-image: none !important
	.title-big
		& + p
			margin-top: 7px
	p 
		&:first-child
			margin-top: 12px
		& + .title-big
			margin-top: 12px
	&__in
		text-align: center
		font-size: 36px
		@extend %cent
	&__but
		margin-top: 25px

// circle
.circle
	position: relative
	width: 152px
	height: 152px
	border-radius: 50%
	background: $dark-gray
	+r(768)
		width: 120px
		height: 120px
	&:before,
	&:after
		position: absolute
		content: ''
		top: 50%
		left: 50%
		transform: translate(-50%,-50%)
		border-radius: 50%
	&:before
		height: 102px
		width: 102px
		border: 10px solid rgba($white, .1)
		+r(768)
			width: 80px
			height: 80px
	&:after
		height: 208px
		width: 208px
		border: 10px solid rgba($white, .6)
		+r(768)
			width: 160px
			height: 160px

// time-day time cost
.time-day,
.time,
.cost
	vertical-align: top
	display: inline-block

.time-day
	+lb

.cost
	span
		+lb

// wrap
.wrap
	text-align: left
	+clr
	+r(768)
		padding-left: 60px

// row
.row
	margin-top: 24px
	+clr
	+r(768)
		margin-top: 28px
	&:first-child
		margin-top: 0

// col
.col
	float: left
	width: calc(100% - 190px)
	letter-spacing: .08em
	+lr
	+r(768)
		width: 100%
	&:first-child
		padding-right: 30px
		width: 190px
		+lb
		text-align: right
		+r(768)
			width: 100%
			text-align: left
			br
				display: none

// error-text
.error-text
	margin-bottom: 5px
	text-align: left
	display: none
	&.is-active
		display: block
	.icon
		margin: 2px 5px 0 0
		color: $red
		vertical-align: top

// tabs
.tabs
	&__cont
		display: none
		+clr

// tab
.tab
	margin: 50px 0
	font-size: 0
	+lb
	li
		margin-left: 35px
		min-width: 172px
		font-size: 12px
		vertical-align: top
		text-align: center
		color: $c_hour
		transition: background .2s, color .2s
		text-transform: uppercase
		background: $bg-tab
		display: inline-block
		&:first-child
			margin-left: 0
		&:hover,
		&.is-active
			color: $black
			background: $yellow
	a
		padding: 7px 15px
		display: block

// table
.table
	margin-top: 19px
	border: 2px solid $bd-table
	&.is-yellow
		border-color: $yellow
		.table__row
			border-color: $yellow
		.table__right
			border-color: $yellow
	&.is-numb
		counter-reset: list
		.table__row
			padding-left: 65px
			&:before
				position: absolute
				counter-increment: list
				content: counter(list)'.'
				width: 42px
				top: 0
				left: 0
				bottom: 0
				+lbd
				text-align: right
				line-height: 37px
	&.is-consult
		.table__row
			line-height: 45px
	&__row
		position: relative
		padding-left: 15px
		width: 100%
		line-height: 35px
		border-top: 1px solid $bd-table
		display: table
		+clr
		&:first-child
			border-top: none
	&__name,
	&__right,
	&__time,
	&__group,
	&__point
		display: table-cell
	&__time,
	&__group
		+lb
	&__right
		width: 60px
		text-align: center
		border-left: 1px solid $bd-table
	&__time
		width: 87px
	&__name
		& + .table__group
			padding-left: 8px
			width: 40px
	&__point
		width: 68%
		font-size: 0
		vertical-align: top
		text-align: center
	&__col
		width: 33.3%
		vertical-align: top
		border-left: 1px solid $bd-table
		display: inline-block
	a
		transition: border-color .2s
		border-bottom: 1px solid transparent
		&:hover
			border-color: $black

// accord
.accord
	& + .accord
		margin-top: -2px
	&.is-active,
	&:hover
		.accord__top
			border-color: $yellow
			background: $yellow
			&:before
				+arr(15,7,$light-black,b)
		& + .accord
			.accord__top
				border-top-color: $yellow
	&.is-active
		margin-top: 0
		.accord__top
			&:before
				transform: rotate(180deg)
		& + .accord
			.accord__top
				border-top-color: $bd-table
	.table
		margin: 0
	.group-name
		padding-right: 8px
		width: 50px
		text-align: center
	.time
		width: 100px
	.theme
		font-size: 12px
		+lr
	.group-name,
	.time,
	.theme,
	.day
		display: table-cell
	.day
		padding-left: 20px
		text-transform: uppercase
	&__top
		position: relative
		padding: 14px 50px 14px 14px
		width: 100%
		cursor: pointer
		+lb
		user-select: none
		transition: border-color .2s, background .2s
		border: 2px solid $bd-table
		&:before
			position: absolute
			content: ''
			right: 25px
			top: 50%
			margin-top: calc(-7px / 2)
			transition: border-color .2s, transform .2s
			+arr(15,7,$bd-table,b)
	&__tabl
		width: 100%
		display: table
	&__block
		display: none

// date
.date
	&.is-bold
		+lb